import React from "react";
import ReactDOM from "react-dom/client";
import App from "./App";
import reportWebVitals from "./reportWebVitals";
import "./style/init";
import store from "./store";
import "./i18n/config";
import { Provider } from "react-redux";
import { CHAIN } from "./config";
import { ConfigProvider } from "react-vant";

const root = ReactDOM.createRoot(document.getElementById("root"));

const themeVars = {
  paginationItemDisabledBackgroundColor: "#141416",
  paginationItemDisabledColor: "#808191",
  paginationBackgroundColor: "#141416",
  paginationItemDefaultColor: "#5997EE",
  checkboxLabelColor: "#ffffff",
  emptyImageSize: "80px",
};

const waitForGlobal = async () => {
  console.log(window.tronWeb, "window.tronWeb");
  if (CHAIN === "SHASTA" || CHAIN === "TRON") {
    // 1. check variable, 检查tronweb是否已经加载
    if (window.tronWeb) {
      //     // 3. 如果一切正常，启动react应用。
      root.render(
        <ConfigProvider themeVars={themeVars}>
          <Provider store={store}>
            <App />
          </Provider>
        </ConfigProvider>
      );
    }else{
        setTimeout(async () => {
            await waitForGlobal();
        }, 100);
    }
  } else {
    root.render(
      <ConfigProvider themeVars={themeVars}>
        <Provider store={store}>
          <App />
        </Provider>
      </ConfigProvider>
    );
  }
};

waitForGlobal().then();

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
