import React, { useEffect, useState } from "react";
import "./index.scss";
import { Popup, Toast, Dialog } from "react-vant";
import UserStoreHook from "../../hook/UserStoreHook";
import { useTranslation } from "react-i18next";
import { getShortAdress } from "../../utils/tool";
import { web3IsAddress } from "../../protocol/web3Util";
import { getUserSFIsActive, userActiveSF, isJihuoSync } from "../coinSFApi";
import logo from "../static/logo.png";
import { useParams } from "react-router-dom";

export default function ActiveStructuredPop(props) {
  const { activeSfCallBack } = props;
  const [visible, setVisible] = useState(false);
  const { user } = UserStoreHook();
  const { invateaddress } = useParams();
  const { t } = useTranslation();
  const [myRecommendUserAddress, setMyRecommendUserAddress] = useState(
    invateaddress || ""
  ); //我的推荐人地址
  const onInputChange = (e) => {
    setMyRecommendUserAddress(e.target.value);
  };
  const confirm = () => {
    if (!myRecommendUserAddress) {
      Dialog.confirm({
        title: t("common.tips"),
        message: t("page.coin.tipText"),
      })
        .then(() => submitActive())
        .catch(() => {});
    } else {
      submitActive();
    }
  };
  //确认激活
  const submitActive = async () => {
    if (!user) {
      return;
    }
    let recommendAddress;
    if (!myRecommendUserAddress) {
      recommendAddress = "T9yD14Nj9j7xAB4dbGeiX9h8unkKLxmGkn";
    } else {
      recommendAddress = myRecommendUserAddress;
      let isjihuo = await isJihuoSync(myRecommendUserAddress);
      console.log(isjihuo, "isjihuo");
      if (!isjihuo) {
        Toast("输入地址未激活");
        return;
      }
    }
    if (user.address.toLowerCase() == recommendAddress.toLowerCase()) {
      Toast(t("page.coin.errorTip"));
      return;
    }
    if (!web3IsAddress(recommendAddress)) {
      Toast(t("page.coin.warnTip"));
      return;
    }

    if (myRecommendUserAddress) {
      const res = await getUserSFIsActive(myRecommendUserAddress);
      if (!res) {
        Toast(t("page.coin.hasnoUser"));
        setMyRecommendUserAddress("");
        return;
      }
    }
    const json = await userActiveSF(recommendAddress);
    if (json) {
      setVisible(false);
      Toast(t("common.active_success"));
    } else {
      Toast(t("common.active_fail"));
    }
  };

  //获取用户是否已激活
  const getUserIsActive = async () => {
    if (!user?.address) {
      return;
    }
    const json = await getUserSFIsActive(user.address).catch(() =>
      setVisible(false)
    );
    console.log(json, "getUserSFIsActive");
    setVisible(!json);
  };

  useEffect(() => {
    getUserIsActive();
  }, [user.address]);

  return (
    <Popup visible={visible} style={{ width: "100%", height: "100%" }}>
      <div className="welcomeModal">
        <div className="flex-row fl-ai-center mb-20">
          <img className="modaltitle mr-10" src={logo} />
          <div className="popupTitle">{t("page.coin.title")}</div>
        </div>
        <div className="modalLabal mt-5">{t("page.coin.myAddress")}</div>
        <div className="modalAddress flex-center">
          {getShortAdress(user?.address || "")}
        </div>
        <div className="modalLabal">{t("page.coin.referrer")}</div>
        <input
          value={myRecommendUserAddress}
          onChange={onInputChange}
          className="modalInput"
          placeholder={t("page.coin.inputaddress")}
        />
        <div className="redcolorText">{t("page.coin.referrerTips")}</div>
        <div className="main-button" onClick={confirm}>
          {t("page.coin.immediatelyApprove")}
        </div>
      </div>
    </Popup>
  );
}
