import {useSelector} from "react-redux";
import UtilHook from "./UtilHook";
import getWeb3 from "../protocol/getWeb3";
import {CHAIN} from "../config";


export default function UserStoreHook() {
    const {dispatch} = UtilHook()
    const {user} = useSelector(state => {
        return {
            user: state.userStore.user,
        }
    })
    const setAddress = async () => {
        if (CHAIN === 'TRON' || CHAIN === 'SHASTA') {
            await getWeb3();
            try {
                dispatch.userStore.setUser({address: window.tronWeb.defaultAddress.base58 || ''})
                window.addEventListener('message', function (e) {
                    if (e.data.message && e.data.message.action == "setAccount") {
                        dispatch.userStore.setUser({address: e.data.message.data.address})
                    }
                })

            } catch (e) {
                console.log("获取用户地址出错");
                console.log(e)
            }
        }
    }
    return {
        user,
        setAddress,

    }
}
