export const web3IsAddress = (value) => {
    return window.tronWeb.isAddress(value)
}

export const web3toHex = (value) => {
    return window.tronWeb.toHex(value)
}


export const web3GetToken = value => {
    return window.tronWeb.address.fromHex(value)
}

