import {Toast, Dialog} from 'react-vant';
import i18next from 'i18next';
import getWeb3 from './getWeb3';
import abiObj from '../abi';
import {showLoading, closeLoading} from "../utils/tool";
import {CHAIN} from "../config";

const contract = {
    SHASTA: tronContract,
    TRON: tronContract
}

const contractList = {};
let sending = false;
let interval;


function tronContract(
    {
        abi = '',
        address = '',
        params = [],
        method = '',
        type = '',
        sendValue = {},
        error,
    },
) {
    return new Promise(async (resolve, reject) => {


        if (!window.tronWeb) await getWeb3();
        const {tronWeb} = window;
        if (!tronWeb) return reject('no web3');
        let contract;
        if (!contractList[abi + address]) {
            let instance = await tronWeb.contract(abiObj[abi], address);
            instance.loadAbi(abiObj[abi]);
            contract = instance
            contractList[abi + address] = contract;
        } else {
            contract = contractList[abi + address];
        }
        if (type === 'call') {
            contract[method](...JSON.parse(JSON.stringify(params))).call({_isConstant: true})
                .then((e) => {
                    if (Object.keys(e).length === 1 && Object.keys(e)[0] != '_hex' || Object.keys(e).length === 2 && JSON.stringify(e[Object.keys(e)[0]]) == JSON.stringify(e[Object.keys(e)[1]])) {
                        resolve(e[Object.keys(e)[0]]);
                    } else {
                        resolve(e);
                    }
                })
                .catch((e) => {
                    console.log(1, address, method, JSON.stringify(params));
                    if (typeof error === 'function') {
                        error();
                    }
                    reject(e);
                });
        } else if (type === 'send') {
            console.log(address, method, params);
            if (sending) return;
            sending = true;
            setTimeout(() => {
                sending = false;
            }, 200);
            // const sendParam = {feeLimit: 1_000_000_000, callValue: sendValue.value || 0, shouldPollResponse: true,};
            try {
                const sendParam = {
                    feeLimit: sendValue.feeLimit || 1_000_000_000,
                    callValue: sendValue.value || 0,
                    shouldPollResponse: false,
                };
                let res = await contract[method](...JSON.parse(JSON.stringify(params))).send(sendParam);
                showLoading()
                interval = setInterval(() => {
                    tronWeb.trx.getUnconfirmedTransactionInfo(res).then(result => {
                        if (result.receipt) {
                            clearInterval(interval);
                            if ((result.receipt.result) === 'REVERT') {
                                console.log(result, '合约方法调用失败');
                               closeLoading()
                                Dialog.alert({
                                    title: i18next.t('common.tips'),
                                    message: i18next.t('utils.contract.tip_fail_tips'),
                                    confirmButtonText: i18next.t('common.close')
                                })
                                clearInterval(interval);
                            } else if ((result.receipt.result) === 'SUCCESS') {
                                let waitTime = 20
                                interval = setInterval(() => {
                                    tronWeb.getEventByTransactionID(res).then(nextResult => {
                                        waitTime -= 1
                                        if (nextResult && nextResult[0]) {
                                            clearInterval(interval);
                                          closeLoading()
                                            console.log('交易完成：', nextResult[0]);
                                            resolve(nextResult[0]);
                                        }
                                        if (waitTime < 0) {
                                            console.log('没有nextResult返回，在区块浏览器查看', address);
                                            clearInterval(interval);
                                           closeLoading()
                                            resolve(true);
                                        }
                                    })
                                }, 1000);
                                // showConfirmModal(i18next.t('common.tips'), '交易已上链，是否等待交易完成', showTip, ()=>{
                                //     Toast.loading()
                                //     interval = setInterval(() => {
                                //         tronWeb.getEventByTransactionID(res).then(nextResult=>{
                                //             if (nextResult[0] && !nextResult[0].unconfirmed) {
                                //                 clearInterval(interval);
                                //                 Toast.loading().clear();
                                //                 console.log('交易完成：',nextResult[0]);
                                //                 resolve(nextResult[0]);
                                //             }
                                //         })
                                //     }, 1000);
                                // });
                            } else {
                                closeLoading()
                                Dialog.alert({
                                    title: i18next.t('common.tips'),
                                    message: i18next.t('common.trade_fail'),
                                })
                            }
                        }
                    });
                }, 1000)

            } catch (error) {
                console.log(error);
            }
        } else {
            if (typeof error === 'function') {
                error();
            }
            return reject('type error');
        }
    });
}

export default contract[CHAIN];
