import React from 'react';
import './index.scss';

export default function Block(props) {
  const { children, bgColor, blockClass = '',onClick } = props;
  return (
    <div
        onClick={onClick}
      className={`common-block ${blockClass}`}
      style={{ backgroundColor: bgColor ? bgColor : '' }}
    >
      {children}
    </div>
  );
}
