import {HashRouter as Router, Routes, Route} from "react-router-dom";
import {useEffect, Suspense} from "react";
import React from "react";
import routeList from './router/index'
import UserStoreHook from "./hook/UserStoreHook";
const domArr = [];
for (let i = 0; i < routeList.length; i += 1) {
  const item = routeList[i];
  domArr.push((
      <Route
          key={i}
          path={item.path}
          exact={item.exact}
          element={item.main}
      />
  ));
}
function App() {
    const {setAddress} = UserStoreHook();
    useEffect(() => {
        setAddress()
    }, [])
  return (
      <Router>
        <Suspense fallback={<div>Loading...</div>}>
              <Routes>{domArr}</Routes>
        </Suspense>
      </Router>
  );
}

export default App;
