import i18next from 'i18next';
import {Dialog} from 'react-vant';
import {CHAIN} from "../config";



const getTronWeb3 = async () => {
  try {
    if(window.tronLink){
      const res = await window.tronLink.request({method: 'tron_requestAccounts'});
      if (res.code === 4001) {
        Dialog.confirm({message: i18next.t('utils.getWeb3.confirm_connection')})
      }
    }
  } catch (e) {
    console.log(e);
  }
}
const getWeb3 = {
  SHASTA: getTronWeb3,
  TRON: getTronWeb3
}
export default getWeb3[CHAIN];
