import React, { useEffect, useState } from "react";
import "./index.scss";
import Block from "../../component/Block";
import { useTranslation } from "react-i18next";
import info from "../info";
import { Arrow } from "@react-vant/icons";
import { BigNumber } from "bignumber.js";
import {
  callAvailable,
} from "../../api/commonApi";
import { copyCot, getResultValue, JcToFixed } from "../../utils/tool";
import UserStoreHook from "../../hook/UserStoreHook";
import { DefaultDecimals } from "../../protocol/constants";
import { Selector, Toast } from "react-vant";
import { useNavigate } from "react-router-dom";
import ActiveStructuredPop from "../component/ActiveStructuredPop";
import {
  isJihuoSync,
  submitPlanSF,
} from "../coinSFApi";
import dayjs from "dayjs";
import utc from "dayjs/plugin/utc";
import StructureFundDescribe from "../component/StructureFundDescribe";

dayjs.extend(utc);
export default function CoinStructuredFundBuy() {
  const [selectPortion, setSelectPortion] = useState(""); //当前选择的认购数量
  
  const [userTRXNum, setUserTRXNum] = useState("0"); //获取用户TRX数量
  const [cycleKey, setCycleKey] = useState(""); //选择的认购周期
  const [showInvated, setShowInvated] = useState(""); //选择的认购周期

  const { t } = useTranslation();
  const { user, setAddress } = UserStoreHook();
  const navigate = useNavigate();

  const getUserTRXNum = async () => {
    let res = await callAvailable("", user.address, DefaultDecimals.TRX);
    console.log(res, "res");
    setUserTRXNum(res);
  };
  const userIsJihuo = async () => {
    let res = await isJihuoSync(user.address);
    console.log(res, "res");
    setShowInvated(res);
  };
  //监听输入框变化
  const onInputChange = (e) => {
    let value = e.target.value;
    setSelectPortion(value);
  };
  //提交
  const planOrder = async () => {
    if (!selectPortion) {
      Toast(t("page.coin.select_limit"));
      return;
    }
    console.log(cycleKey,selectPortion,'userTRXNum');
    if (BigNumber(userTRXNum).isLessThan(BigNumber(selectPortion))) {
      Toast("TRX" + t("common.balance_lack"));
      return;
    }
    const json = await submitPlanSF(cycleKey,selectPortion).catch(() => {});
    if (json) {
      Toast(t("page.coin.plan_success"));
      getUserTRXNum();
      userIsJihuo()
      setCycleKey('')
      setSelectPortion('')
    } else {
      Toast(t("page.coin.plan_fail"));
    }
  };

  useEffect(() => {
    if (user.address) {
      getUserTRXNum();
      userIsJihuo()
    } else {
      setAddress();
    }
  }, [user.address]);

  return (
    <div className="buyContent">
      <StructureFundDescribe
        title={t("page.coin.title")}
        day={info.cycle}
        profit={`${info.profit * 100}%`}
      />

      <Block>
        <div className="font-color-white font-size-16 mb-20 font-weight-500">
          {t("page.coin.buy_supply")}
        </div>
        <div className="mb-10">
          {t("page.coin.coin_can_use", { coin: "TRX" })}
          {userTRXNum}
        </div>

        <div className="mb-10">
          <input
            value={selectPortion}
            onChange={onInputChange}
            className="buyfenInput mb-10"
            placeholder={t("page.coin.inputtext")}
          />
        </div>
        <div className="mb-10">
          <div className="mb-10">选择理财周期</div>
          <Selector
            options={info.options}
            defaultValue={[]}
            value={[cycleKey]}
            onChange={(arr, extend) => setCycleKey(extend.items[0].value)}
          />
        </div>
        <div className="button-click  main-button mt-15" onClick={planOrder}>
          {t("page.coin.submit_apply")}
        </div>
      </Block>
      {showInvated?<Block>
        <div className="font-size-14 font-color-white font-weight-400 mb-10">
          {t("page.coin.invated")}
        </div>
        <div
          className="button-click  main-button "
          onClick={() => {
            copyCot(`${window.location.origin}${window.location.pathname}#/buy/${user.address}`);
          }}
        >
          {t("page.coin.invated")}
        </div>
      </Block>:null}
      <Block onClick={() => navigate("/FundDetail")}>
        <div className="flex-row fl-ai-center fl-jc-between">
          <div className="font-size-14 font-color-white font-weight-400">
            {t("common.my_position")}
          </div>
          <Arrow className="font-color-describe font-size-16" />
        </div>
      </Block>

      <ActiveStructuredPop />
    </div>
  );
}
