// 智能合约接口
import contract from "../protocol/contract";
import {getResultValue, JcToFixed} from "../utils/tool";
import address from "../abi/address";
import {BigNumber} from 'bignumber.js'
import {DefaultDecimals} from "../protocol/constants";

// 获取可用余额
export async function callAvailable(token, address, decimal) {
    if (address == '') return '0';
    let res = '0';
    try {
        if (token !== '') {
            res = await contract({
                abi: 'erc20',
                address: token,
                method: 'balanceOf',
                params: [address],
                type: 'call',
            });
        } else {
            res = await window.tronWeb.trx.getAccount(address)
            res = res.balance
        }
    } catch (e) {
        console.log('error');
        console.log(e);
    }
    return JcToFixed(getResultValue(res, decimal));
}

export async function callAllowance(token, owner, spender, decimal) {
    if (!owner || !token || !spender || !decimal) return false;
    const res = await contract({
        abi: 'erc20',
        address: token,
        method: 'allowance',
        params: [owner, spender],
        type: 'call',
    });
    return !BigNumber(getResultValue(res, decimal)).isLessThan((100000000));
}

// 激活权限
export async function goApprove(fromAddress, enterAddress) {
    return await contract({
        abi: 'erc20',
        address: fromAddress,
        method: 'approve',
        params: [enterAddress, '0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff'],
        type: 'send',
    });
}

