//提交购买
export const submitPlanSF = async (projectId, amount) => {
  return await contract({
    abi: "StableFundOrder",
    address: address.StableFundOrder,
    method: "deposit",
    params: [projectId],
    type: "send",
    sendValue: {
      value: getSendValue(amount, DefaultDecimals.TRX),
    },
  });
};

