// 排单列表
export const getDepositListSync = async (userAddress, pagestart, limt) => {
  let length = await contract({
    abi: "StableFundOrder",
    address: address.StableFundOrder,
    method: "getUserOrderIdsLength",
    params: [userAddress],
    type: "call",
  });
  let lengthArr = new Array(parseInt(length.toString()))
    .fill("1")
    .map((i, inx) => inx)
    .slice((pagestart - 1) * limt, pagestart * limt)
    .map((it) => ({
      abi: "StableFundOrder",
      address: address.StableFundOrder,
      method: "getOrderByUserKey",
      params: [userAddress, it],
    }));
  let arrlist =  await multicall(lengthArr);
  return{
    arrlist,
    count:length.toString()
  }
};

