// 默认保留6位有效数字
export function JcToFixed(number, position = 6) {
    if (!number || number === '等待中。。。' || isNaN(number)) {
        return '0';
    }
    if (!isNaN(number)) {
        number = number.toString();
    }
    let arr = number.split('.');
    if (arr.length == 1) {
        return new BigNumber(arr[0]).toFixed();
    }
    if (arr.length == 2) {
        let len = arr[1].length >= position ? position : arr[1].length;
        return new BigNumber(number).toFixed(len, 1);
    } else {
        console.log('number error');
        throw new Error('number error');
    }
}

//定时器hooks方式实现
export function useInterval(callback, delay) {
    const savedCallback = useRef();

    // 保存新回调
    useEffect(() => {
        savedCallback.current = callback;
    });

    // 建立 interval
    useEffect(() => {
        function tick() {
            savedCallback.current();
        }

        if (delay !== null) {
            let id = setInterval(tick, delay);
            return () => clearInterval(id);
        }
    }, [delay]);
}


// 将秒数转换为天时分秒
export function formatSeconds(value) {

    let result = '';
    if (!value) {
        return '0';
    }
    let sencond = parseInt(value);
    if (sencond > 3600 * 24) {  //表示大于一天
        let day = sencond / (3600 * 24);
        sencond = sencond % (3600 * 24);
        let minute = sencond % 3600 / 60; //求天数整除外剩余的秒数
        let hour = sencond / 3600;          //1小时==60分钟==3600秒，所以剩余的秒数÷3600= 得到几个小时数
        if (parseInt(hour) > 0) {
            if (parseInt(minute) > 0) {
                result = parseInt(day) + '天' + parseInt(hour) + '小时' + parseInt(minute) + '分钟';
            } else {
                result = parseInt(day) + '天' + parseInt(hour) + '小时';
            }
        } else {
            result = result = parseInt(day) + '天';
        }
    } else if (sencond > 3600) {     //表示大于一个小时
        let hour = sencond / 3600;
        let minute = sencond % 3600 / 60;         //求小时数整除外剩余的秒数， 秒数÷60秒 = 得到几分钟
        if (parseInt(minute) > 0) {
            result = parseInt(hour) + '小时' + parseInt(minute) + '分钟';
        } else {
            result = parseInt(hour) + '小时';
        }
    } else if (sencond > 60) {  //表示大于一分钟
        let minute = sencond / 60;
        let sec = sencond % 60;           //求分钟数整除外剩余的秒数
        if (parseInt(sec) > 0) {
            result = parseInt(minute) + '分钟' + parseInt(sec) + '秒';
        } else {
            result = parseInt(minute) + '分钟';
        }
    } else {  //不大于一天、不大于一个小时、也不大于一分钟，那就是秒数
        result = '' + parseInt(sencond) + '秒';
    }
    return result;
}

//获取到现在的时间到传入的时间的天数返回多少天 大于是正数小于是负数 传入是一个时间戳单位毫秒，
//一个是小于1天的时候是否忽略展示为1天
export function getDelayDay(delayDay, ignoreDay = true) {
    let days;
    const delay = dayjs(parseInt(delayDay))
        .format('YYYY-MM-DD HH:mm:ss');
    const now = dayjs()
        .format('YYYY-MM-DD HH:mm:ss');
    days = dayjs(delay)
        .diff(dayjs(now), 'days');
    if (days === 0) {
        if (ignoreDay) {
            days = 1;
        } else {
            days = dayjs(delay)
                .diff(dayjs(now), 'seconds');
            console.log(days)
            const daySecond = 60 * 60 * 24;
            days = days / daySecond;

            days = JcToFixed(days);
        }
    }
    return days;
}

/**
 * 数字以千分位添加逗号
 * @param num
 * @returns {string}
 */
export function addDou(num) {
    if (num == 0) {
        return '0';
    }
    if (!num) {
        return '等待中。。。';
    }
    if (typeof num === 'number') {
        num = num.toFixed(2);
    }
    if (typeof num === 'string') {
        num = parseFloat(num)
            .toFixed(2)
            .toString();
    }

    const numArr = num.split('.');
    num = numArr[0];
    let result = '';
    while (num.length > 3) {
        result = ',' + num.slice(-3) + result;
        num = num.slice(0, num.length - 3);
    }
    if (num) {
        result = num + result;
    }
    result = result + '.' + numArr[1];
    return result;
}


// 乘法
export const valueMultiplied = (value, multiplied = 1, retain = 6) => {
    const returnnums = new BigNumber(value);
    const multipliednums = new BigNumber(multiplied);
    return (returnnums.multipliedBy(multipliednums));
};


// 简化地址
export function getShortAdress(adress) {
    if (!adress) {
        return '';
    }
    const pre = adress.substring(0, 5);
    const sub = adress.substring(
        adress.length - 4,
        adress.length,
    );
    return `${pre}...${sub}`; // 返回一个函数
}

//按长度拆分数组
export function cutArrayBuyLength(array, subLength) {
    let index = 0;
    let newArr = [];
    while (index < array.length) {
        newArr.push(array.slice(index, index += subLength));
    }

    return newArr;
}


// 新增防抖
export function useDebounce(fn, delay, dep = []) {
    const {current} = useRef({fn, timer: null});
    useEffect(() => {
        current.fn = fn;
    }, [fn]);

    return useCallback(function f(...args) {
        if (current.timer) {
            clearTimeout(current.timer);
        }
        current.timer = setTimeout(() => {
            current.fn.call(this, ...args);
        }, delay);
    }, dep);
}

//将名称中usdt/v42替换为usdt-v42
export function replacePoolName(name) {
    if (!name) {
        return ''
    }
    return name.replace(new RegExp('/', 'g'), '-');
}

//复制文本
export function copyCot(cot) {
    const pEle = document.createElement('p');
    pEle.innerHTML = cot || '';
    document.body.appendChild(pEle);
    const range = document.createRange(); // 创造range
    window.getSelection().removeAllRanges(); //清除页面中已有的selection
    range.selectNode(pEle); // 选中需要复制的节点
    window.getSelection().addRange(range); // 执行选中元素
    const copyStatus = document.execCommand("Copy"); // 执行copy操作
    Toast('复制成功')
    document.body.removeChild(pEle);
    window.getSelection().removeAllRanges(); //清除页面中已有的selection
}


export function gettimeText(data) {
    if (data && data > 0) {
        const cutTime = data;
        const day = Math.floor(cutTime / (60 * 60 * 24));
        const hour = Math.floor((cutTime / (60 * 60)) % 24);
        const minute = Math.floor((cutTime / 60) % 60);
        const second = Math.round(cutTime % 60);
        return `${day ? `${day}天` : ''}${hour ? `${hour}小时` : ''}${
            minute ? `${minute}分钟` : ''
        }${second ? `${second}秒` : ''}`;
    }
};
