import contract from "../protocol/contract";
import address from "../abi/address";
import abiObj from '../abi';
import { CHAIN } from "../config";


const encodeCallData = (callObj) => {
    if (CHAIN == 'TRON' || CHAIN == 'SHASTA') {
        const  contract = window.tronWeb.contract(abiObj[callObj.abi])//, callObj.address)
        callObj.contract = contract;
        const signature = contract.methodInstances[callObj.method].signature;

        // const paramsdata = tronWeb.utils.abi.encodeParamsV2ByABI(
        //         contract.methodInstances[callObj.method].abi,
        //         callObj.params
        // );
        let types = []
        let values = []
        const inputs = contract.methodInstances[callObj.method].abi.inputs
        for (let i=0; i < inputs.length; i++) {
            const type = inputs[i].type
            let value = callObj.params[i]
            if (type === 'address')
                value = window.tronWeb.address.toHex(value).replace(/^(41)/, '0x');
            else if (type.match(/^([^\x5b]*)(\x5b|$)/)[0] === 'address[')
                value = value.map(v => window.tronWeb.address.toHex(v).replace(/^(41)/, '0x'));
            // console.log('type=', type, ' value=', value)
            types.push(type)
            values.push(value)
        }
        const abiCoder = new window.tronWeb.utils.ethersUtils.AbiCoder()
        const paramsdata = abiCoder.encode(types, values)

        const hexAddress = '0x' + window.tronWeb.address.toHex(callObj.address).substr(2);
        return [hexAddress, '0x' + signature + paramsdata.substr(2)];
    }
}

const decodeReturnData = (callObjs, res) => {
    let retVal = [];
    for (let i=0; i < callObjs.length; ++i) {
        let decoded;
        if (res[i].success) {
            if (CHAIN == 'TRON' || CHAIN == 'SHASTA') {
                decoded = window.tronWeb.utils.abi.decodeParams(
                    callObjs[i].contract.methodInstances[callObjs[i].method].abi.outputs,
                    res[i].returnData
                )
            }
            if (decoded.length == 1) {
                decoded = decoded[0]
            }
        }
        retVal.push(decoded);
    }
    return retVal
}

export default async function multicall(params=[]) {
    if (!Array.isArray(params) || params.length <= 10) {
        !Array.isArray(params) && console.error(`入参非数组，不可执行`,`数组长度需要大于2，数组长度是：${params.length}`);
        if (params.length <= 10) {
            let res =  Promise.all(
                params.map((items)=>contract({
                    abi: items.abi,
                    address: items.address,
                    method: items.method,
                    params: items.params,
                    type: 'call',
                }))
            )
            return res
        }
        return false
    }
    const calldata = await Promise.all(params.map(encodeCallData))
    let res =  await contract({
        abi: 'multicall',
        address: address.Multicall,
        method: "tryAggregateCall",
        params: [calldata],
        type: 'call',
    });
    return decodeReturnData(params, res)
}
